import React from 'react'
import Link from 'gatsby-link'


export default class IndexPage extends React.Component {
    render() {
        const { data } = this.props;
        const fun = data.allContentstackFun.edges;
        console.log("fun",fun)
        return (
            <div className="container">
                {fun.map(edge => {
                    return(
                        <div key={edge.node.id} className="fun">
                            <section>
                                <div className="fun-title">
                                    <h2>{edge.node.title}</h2>
                                </div>
                                <div className="fun-description">
                                    {edge.node.short_title}

                                </div>
                            </section>
                        </div>
                    )
                })}
            </div>
        )
    }
}


export const pageQuery = graphql`
    query FunQuery {
      allContentstackFun(
        limit: 1000,
        sort: { order: ASC, fields: [created_at] }
            ) {
        edges {
          node {
            id
            url
            title
            
          }
        }
      }
    }
`


